/**
 * Use [`Field`](./number.ts.html#Field) instead.
 *
 * @category instances
 * @since 2.0.0
 * @deprecated
 */

export var fieldNumber = {
  add: function add(x, y) {
    return x + y;
  },
  zero: 0,
  mul: function mul(x, y) {
    return x * y;
  },
  one: 1,
  sub: function sub(x, y) {
    return x - y;
  },
  degree: function degree(_) {
    return 1;
  },
  div: function div(x, y) {
    return x / y;
  },
  mod: function mod(x, y) {
    return x % y;
  }
};